// import $ from 'jquery'
import toastr from 'toastr';
toastr.options = {
  'closeButton': true,
  'timeOut': '2000'
};
import toggleButton from '../toggleButton.vue';
export default {
  components: {
    'toggle-button': toggleButton
  },
  props: {
    assign_items: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    data_rolegroup: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    data_uyquyen: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    type: {
      type: Number,
      default: function _default() {
        return 1;
      }
    },
    configNote: {
      type: Object,
      default: function _default() {}
    },
    detailDossier: {
      type: Object,
      default: function _default() {}
    }
  },
  model: {
    prop: 'assign_items'
  },
  watch: {// data_rolegroup: {
    //   handler: function (value) {
    //     let vm = this
    //     console.log('valWatch123', value)
    //     vm.data_rolegroup = value
    //   },
    //   deep: true
    // },
  },
  data: function data() {
    return {
      reRender: true,
      toUsersExport: [],
      data_phancong: [],
      assignedtype_items: {
        '2': [{
          text: 'Không có phân công',
          value: 0
        }, {
          text: 'Thực hiện chính',
          value: 1
        }, {
          text: 'Thực hiện phối hợp',
          value: 2
        }],
        '3': [{
          text: 'Không có phân công',
          value: 0
        }, {
          text: 'Thực hiện chính',
          value: 1
        }, {
          text: 'Thực hiện phối hợp',
          value: 2
        }, {
          text: 'Theo dõi',
          value: 3
        }]
      },
      title_asign: {
        '1': 'PHÂN CÔNG NGƯỜI THỰC HIỆN',
        '2': 'PHÂN CÔNG THỰC HIỆN, PHỐI HỢP',
        '3': 'PHÂN CÔNG THỰC HIỆN, PHỐI HỢP VÀ THEO DÕI'
      },
      presenterAddGroup: true,
      assignValidate: true
    };
  },
  created: function created() {
    var vm = this;
    setTimeout(function () {
      window.$('.btn-tgl').addClass('btn-hidden');
    }, 200);
  },
  mounted: function mounted() {
    var vm = this;
    vm.data_phancong = vm.assign_items;

    if (vm.type === 8 || vm.type === 9) {
      vm.data_rolegroup = vm.data_rolegroup.filter(function (item) {
        return !vm.checkUserRole(item.roleCode);
      });
    }
  },
  methods: {
    changeAssigned: function changeAssigned(event, index) {
      var vm = this;

      if (vm.type !== 0) {
        if (event === true) {
          if (vm.type === 5) {
            for (var key in vm.assign_items) {
              vm.assign_items[key].assigned = 0;
            }

            vm.assign_items[index].assigned = 1;
          } else {
            vm.assign_items[index].assigned = 1;
          }
        } else {
          vm.assign_items[index].assigned = 0;
        }
      }
    },
    checkAssignedRoleGroup: function checkAssignedRoleGroup(event, index) {
      var vm = this;

      if (vm.type !== 0) {
        if (event === true) {
          if (vm.type === 7 || vm.type === 9) {
            for (var key in vm.data_rolegroup) {
              vm.data_rolegroup[key].assigned = 0;
            }

            vm.data_rolegroup[index].assigned = 1;
          } else if (vm.type === 6 || vm.type === 8) {
            vm.data_rolegroup[index].assigned = 1;
          }
        } else {
          vm.data_rolegroup[index].assigned = 0;
        }
      }

      var toUsers = [];

      for (var _key in vm.data_rolegroup) {
        if (vm.data_rolegroup[_key].assigned === 1) {
          var usersAssign = vm.data_rolegroup[_key].hasOwnProperty('lstUser') && vm.data_rolegroup[_key]['lstUser'] ? vm.data_rolegroup[_key]['lstUser'] : [];

          if (!Array.isArray(usersAssign)) {
            usersAssign = [usersAssign];
          }

          usersAssign = usersAssign.map(function (item) {
            item = Object.assign(item, {
              assigned: 1
            });
            return item;
          });
          toUsers = toUsers.concat(usersAssign);
        }
      }

      vm.toUsersExport = toUsers;
      vm.reRender = false;
      vm.$nextTick(function () {
        vm.reRender = true;
      });
    },
    changeDelegacy: function changeDelegacy(event, index) {
      var vm = this;

      if (vm.type === 3 || vm.type === 4) {
        if (event === true) {
          vm.data_uyquyen[index].assigned = 1;
        } else {
          vm.data_uyquyen[index].assigned = 0;
        }
      }
    },
    checkAsign: function checkAsign(event, index) {
      var vm = this;

      if (event) {
        vm.assign_items[index].assigned = 1;
        window.$("#btn-".concat(index)).removeClass('btn-hidden');
      } else {
        vm.assign_items[index].assigned = 0;
        window.$("#btn-".concat(index)).addClass('btn-hidden');
      } // console.log('vm.assign_items', vm.assign_items)

    },
    changeTypeAssign: function changeTypeAssign(event, index) {
      var vm = this;

      if (event.value) {
        vm.assign_items[index].assigned = 1;
        window.$("#btn-".concat(index)).removeClass('btn-hidden');
      } else {
        vm.assign_items[index].assigned = 2;
        window.$("#btn-".concat(index)).addClass('btn-hidden');
      } // console.log('vm.assign_items', vm.assign_items)

    },
    doReAsign: function doReAsign() {
      var vm = this;
      var filter = {
        'dossierId': vm.detailDossier.dossierId,
        'users': vm.data_uyquyen
      };
      vm.$store.dispatch('postDossierUserAsign', filter).then(function (result) {
        toastr.success('Yêu cầu của bạn thực hiện thành công');
      }).catch(function (error) {});
    },
    doExport: function doExport() {
      var vm = this;
      var assign;

      if (vm.type === 6 || vm.type === 7 || vm.type === 8 || vm.type === 9) {
        assign = vm.toUsersExport.filter(function (item) {
          return Number(item.assigned) > 0;
        });
      } else {
        assign = vm.assign_items.filter(function (item) {
          return Number(item.assigned) > 0;
        });
      }

      if (assign.length === 0) {
        vm.assignValidate = false;
        return vm.assignValidate;
      } else {
        vm.assignValidate = true;
        return vm.assignValidate;
      }
    },
    getToUsersExport: function getToUsersExport() {
      var vm = this;
      return vm.toUsersExport;
    },
    getDataDelegacy: function getDataDelegacy() {
      var vm = this;
      return vm.data_uyquyen;
    },
    checkUserRole: function checkUserRole(roleItem) {
      var vm = this;
      var roles = vm.$store.getters.getUser.role;

      if (!roles) {
        return false;
      }

      var roleExits = roles.findIndex(function (item) {
        return item === roleItem;
      });
      return roleExits >= 0;
    }
  }
};